@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;1,300&display=swap');
.heroTop{
    width: 100%;
    height: 100vh;
    background-color: #EEFBF3;
}
.heroTop .heroMain{
   display: flex;
   justify-content: space-between;
   width: 90%;
   margin: auto;
   height: 100vh;
   max-width: 1266px;
  
   align-items: center;
}

.heroTop .heroMain .heroLeft,
.heroTop .heroMain .heroRight{
    width: 50%;
    height: 100vh;

}
.heroTop .heroMain .heroLeft {
    display: flex;
    align-items: center;
    
}
.heroTop .heroMain .heroLeft .heroLeftInner{
    margin-top: 35px;
}
.heroTop .heroMain .heroLeft .heroLeftInner h3{
    font-size: 20px;
    font-family: 'Open Sans', sans-serif;
    font-weight: 600;
    color: #309255;
    margin-bottom: 20px;
}
.heroTop .heroMain .heroLeft .heroLeftInner h1{
    font-size: 45px;
    font-family: 'Open Sans', sans-serif;
    line-height: 55px;
}
.heroTop .heroMain .heroLeft .heroLeftInner .getACourse{
  padding: 10px 25px;
  margin-top: 35px;
  cursor: pointer;
  background-color: #309255;
  color: #fff;
  outline: none;
  box-shadow: none;
  border: none;
  border-radius: 5px;
  font-size: 17px;
  font-family: 'Open Sans', sans-serif;
}
.heroTop .heroMain .heroRight {
    display: flex;
    align-items: center;
    position: relative;
    
}

.heroTop .heroMain .heroRight img{
    width: 100%;
    bottom: 0px;
    position: absolute;

}
