@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap');
.FindCourseTop{
    width: 90%;
    margin: auto;
    max-width: 1266px;

    margin-top: 40px;
    margin-bottom: 30px;
}
.FindCourseTop .FindCourseHeader {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.FindCourseTop .FindCourseHeader h3{
    font-size: 20px;
    font-family: 'Open Sans', sans-serif;
}

.FindCourseTop .FindCourseHeader .viewAllBtn{
    padding: 10px 30px;
    background-color: #309255;
    color: #fff;
    border: none;
    outline: none;
    box-shadow: none;
    border-radius: 5px;
    cursor: pointer;
}



.FindCourseTop .findCategory{
    margin-top: 30px;
    display: flex;
    background-color: #EEFBF3;
}
.FindCourseTop .findCategory div{
    padding: 10px 20px;   
    font-family: 'Open Sans', sans-serif;
    font-size: 17px;
    font-weight:bold;
    cursor: pointer;
}

.FindCourseTop .findCategory .findActive{
    color: #309255;
    border-bottom: 2px solid #309255;
}



/* tab content start here =============================== */
.FindCourseTop .courseCategoryContent{
    margin-top: 30px;
}
.FindCourseTop .courseCategoryContent .tabContent{
   display: none;
   transition: 0.3s;
}
.FindCourseTop .courseCategoryContent .findActiveContent{
    display: block;
}