@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap');
.HeaderTop{
    width: 100%;
   z-index: 10;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    padding: 15px 5%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: transparent;
}
.HeaderTop .left{
   display: flex;
   align-items: center;
}
.HeaderTop .left h1{
    margin-right: 20px;
}
.HeaderTop .left a{
    margin: 0px 15px;
    text-decoration: none;
    color: #000;
    font-family: 'Open Sans', sans-serif;
    font-size: 17px;
    font-weight:bold;
}

.HeaderTop .right{
  display: flex;
  align-items: center;
}
.HeaderTop .right .signIn{
    padding: 10px 25px;
    border-radius: 5px;
    background-color: transparent;
    border: none;
    font-size: 17px;
    cursor: pointer;
}
.HeaderTop .right .signUp{
    padding: 10px 25px;
    border-radius: 5px;
    font-size: 17px;
    background-color: #fff;
    border: 1px solid #309255;
    cursor: pointer;
}